/* setting 칼라*/
$theme-color_b: #3692ff;
$theme-color_g: #4b5563;

/* break point */
$mobile: 376px;
$tablet: 745px;

/* 모바일 376 이하*/
@mixin mobile {
  @media (max-width: $mobile) {
    @content;
  }
}

/* 타블렛 744 이하*/
@mixin tablet {
  @media (max-width: $tablet) {
    @content;
  }
}
