@import "./utils.scss";

/* Common */
html {
  font-size: 16px;
  word-break: keep-all;
  line-height: 1.5;
}
body,
div,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
dl,
dt,
dd,
table,
th,
td,
form,
fieldset,
legend,
input,
a,
button,
select,
figure {
  margin: 0;
  padding: 0;
  font-family: "Noto Sans KR", sans-serif;
  box-sizing: border-box;
  word-break: keep-all;
}
img,
fieldset {
  border: 0;
}
ul,
ol {
  list-style: none;
}
em,
address {
  font-style: normal;
}
a {
  text-decoration: none !important;
}
button,
input {
  outline: none;
  border: 0;
  cursor: pointer;
}

/* HEADER */
header {
  padding: 10px 200px;
  display: flex;
  align-items: center;
  gap: 20px;
  border-bottom: 1px solid #dfdfdf;
  nav {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .nav_left {
      display: flex;
      gap: 20px;
      li {
        cursor: pointer;
        &:hover {
          font-weight: bold;
        }
      }
    }
    .nav_right {
      li {
        button {
          font-size: 17px;
        }
      }
    }
  }
}
.blue_btn {
  padding: 12px 23px;
  background-color: $theme-color_b;
  color: #fff;
  border-radius: 8px;
}
.content_wrap {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  .content_box {
    padding: 0 0 100px 0;
  }
}
@include tablet {
  header {
    padding: 5px 24px;
  }
  .content_wrap {
    padding: 24px;
    .content_box {
      padding: 0 0 100px 0;
    }
  }
}
/* PAGE : ITEMS */
.title_box {
  padding: 16px 0;
  display: flex;
  justify-content: space-between;
  .sub_ttl {
    font-size: 20px;
    font-weight: bold;
  }
  .sub_search_box {
    display: flex;
    align-items: center;
    gap: 10px;
    .search_inform {
      form {
        input {
          width: 325px;
          padding: 10px 20px;
          background-color: #f3f4f6;
          border-radius: 8px;
        }
      }
    }
    .items_order {
      .select_order {
        padding: 10px 20px;
        border-radius: 8px;
        outline: none;
        border: 1px solid #e5e7eb;
        option {
          padding: 10px 20px;
          border: 1px solid red;
        }
      }
    }
  }
}
.items_card {
  .img_wrap {
    border-radius: 16px;
    overflow: hidden;
    height: 221px;
    img {
      width: 100%;
      height: 100%;
      object-fit: fill;
    }
  }
  .text_wrap {
    .description {
      display: block;
      font-size: 14px;
    }
    .heart_wrap {
      display: flex;
      align-items: center;
      gap: 5px;
      span {
        line-height: 10px;
      }
    }
  }
}
.best_items_area {
  .best_items_list {
    ul {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 16px;
      li {
      }
    }
  }
}
.all_items_area {
  .all_item_list {
    ul {
      display: grid;
      grid-template-columns: repeat(5, 1fr);
      gap: 16px;
    }
  }
}
.page_navi {
  padding: 20px 0;
  display: flex;
  justify-content: center;
  gap: 10px;
  .page_list {
    display: flex;
    gap: 4px;
  }
  .navi_btn {
    width: 40px;
    height: 40px;
    border-radius: 100%;
    background-color: #fff;
    border: 1px solid #e5e7eb;
    font-size: 16px;
    &:hover {
      background-color: $theme-color_b;
      color: #fff;
    }
  }
}
@include tablet {
  /* 브라우저 사이즈 744이하일때 */
  .best_items_area {
    .best_items_list {
      ul {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 16px;
        li {
        }
      }
    }
  }
  .all_items_area {
    .all_item_list {
      ul {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 16px;
      }
    }
  }
}

@include mobile {
  /* 브라우저 사이즈 376 이하일때 */
  .best_items_area {
    .best_items_list {
      ul {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        gap: 16px;
        li {
        }
      }
    }
  }
  .all_items_area {
    .all_item_list {
      ul {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 16px;
      }
    }
  }
}
